.loader-wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
}

.loader {
  border: 10px solid #e3e3e3;
  border-top: 10px solid #5334db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
